/* Mixins */

@mixin telefono{
    @media(min-width: 480px){
        @content;
    }
}

@mixin tablet{
    @media(min-width: 768px){
        @content;
    }
}

@mixin desktop{
    @media(min-width: 1200px){
        @content;
    }
}

@mixin large{
    @media(min-width: 1600px){
        @content;
    }
}