/* Variables */
/* Globales */
html {
  box-sizing: border-box;
  font-size: 62.5%; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-size: 1.6rem;
  font-family: "Lato", sans-serif;
  line-height: 1.2; }

a {
  text-decoration: none; }

ul {
  list-style: none;
  padding: 0; }

table {
  border-spacing: 0; }

/* Fuentes */
h1, h2, h3 {
  line-height: 1.4; }

h1 {
  font-size: 2.2rem; }

h2 {
  font-size: 3rem; }

h3 {
  font-size: 2rem; }

/* Utilidades */
.contenedor {
  max-width: 1400px;
  width: 95%;
  margin: 0 auto; }

.seccion {
  margin-top: 2rem;
  margin-bottom: 2rem; }

.text-light {
  font-weight: 300; }

.text-center {
  text-align: center; }

.seleccionado {
  background-color: #2c7a7b; }

.contenedor-overflow {
  overflow-x: auto;
  width: 100%; }

.contenido-centrado {
  max-width: 50rem; }

.contenido-principal {
  padding: 2rem 0; }

.w-100 {
  width: 100%; }

.alerta {
  padding: 1rem 1.5rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  margin: 1rem 0; }

.error {
  background-color: #8C111B;
  color: #FFFFFF; }

.contenedor-imagen {
  height: 4.3rem;
  width: 4.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #329932;
  border-radius: 50%; }
  .contenedor-imagen .fa-node {
    color: #FFFFFF;
    font-size: 2.5rem; }

.imagen {
  width: 4.4rem; }

/* Mixins */
/* Boton */
.btn {
  padding: 1rem 1.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  border: none;
  width: 100%;
  margin-top: 2rem; }
  .btn:hover {
    cursor: pointer; }

.btn-verde {
  background-color: #319795; }
  .btn-verde:hover {
    background-color: #2c7a7b; }

/* Layout */
.header {
  background-color: #319795;
  padding: 4rem 1rem;
  color: #FFFFFF; }
  @media (min-width: 768px) {
    .header {
      min-height: 100%; } }
  .header .header-titulo {
    text-transform: uppercase;
    font-weight: 900; }
  .header .header-parrafo {
    margin: 3.5rem 0; }

.navegacion {
  margin-top: 2rem;
  display: flex;
  flex-direction: column; }
  .navegacion a {
    color: #FFFFFF;
    padding: 1rem;
    margin-bottom: .6rem; }
    .navegacion a:hover {
      background-color: #2c7a7b;
      color: #F2E205; }
    .navegacion a:last-of-type {
      margin-bottom: 0; }

/* División de la página */
.pagina {
  min-height: 100vh;
  background-color: #F2F2F2; }
  @media (min-width: 768px) {
    .pagina {
      display: grid;
      grid-template-columns: 1fr 2fr; } }
  @media (min-width: 1200px) {
    .pagina {
      grid-template-columns: 1fr 3fr; } }
  @media (min-width: 1600px) {
    .pagina {
      grid-template-columns: 1fr 4fr; } }

/* tabla-cliente */
.tabla-clientes {
  border-collapse: collapse;
  min-width: 100%; }
  .tabla-clientes th {
    background-color: #D9D9D9;
    color: #595956;
    text-transform: uppercase;
    padding: 1.5rem 2.5rem;
    text-align: left;
    font-size: 1.3rem;
    font-weight: 700; }
  .tabla-clientes td {
    padding: 1rem 2rem 1.5rem 2.5rem;
    background-color: #FFFFFF;
    border-bottom: solid 1.5px #F2F2F2;
    color: #595956; }
  .tabla-clientes .tabla-datos .nombre {
    font-size: 1.8rem;
    font-weight: 700;
    margin: 1rem 0; }
  .tabla-clientes .tabla-datos .correo {
    font-size: 1.4rem;
    margin: .5rem 0 0 0; }
  .tabla-clientes .tabla-empresa {
    color: #7B838C; }
  .tabla-clientes .tabla-acciones {
    display: flex;
    flex-wrap: wrap; }
    .tabla-clientes .tabla-acciones a {
      margin-right: 2rem; }
      .tabla-clientes .tabla-acciones a:last-of-type {
        margin-right: 0; }
    .tabla-clientes .tabla-acciones .actualizar {
      color: #44A65C; }
    .tabla-clientes .tabla-acciones .eliminar {
      color: #D92B2B; }

/* .formulario */
.formulario-clientes {
  padding: 2rem;
  background-color: #FFFFFF;
  border-radius: 1rem; }

.campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.8rem; }
  .campo:last-of-type {
    margin-bottom: 0; }
  .campo label {
    font-weight: 700;
    margin-bottom: .5rem;
    color: #595956; }
  .campo input {
    padding: 1rem 1.5rem;
    border: solid 1px #E1E1E1;
    border-radius: .5rem; }
    .campo input::placeholder {
      color: #adadad; }
    .campo input:focus {
      outline: none;
      border: solid 2px #88C9F2; }
